/*===================
PARAMATERS=====================*/
$font-main: 16px;
$fontsize-title: 30px;


/*===================
COLOR=====================*/
$blue-headline: #003069;
$blue-content: #0063b5;
$light-blue-for-block: #d0e1f2;
$gray: #929090;
/*===================
FONTS=====================*/
@mixin FontRegular ( $value, $line:1.4 ) {
	font-family: arial, helvetica, sans-serif;
	font-size: $value;
	line-height: $line;
	font-weight: normal;
}

@mixin FontBold ( $value, $line:1.4 ) {
	font-family: arial, helvetica, sans-serif;
	font-size: $value;
	line-height: $line;
	font-weight: normal;
}
/*===================
FUNCTIONS=====================*/
@function em($size) {
	@return #{$size / 13px}em;
}

/*===================
MIXIN=====================*/
@mixin animate($time){
	-webkit-transition: all $time;
	-moz-transition: all $time;
	transition: all $time;
}

@mixin selectNone(){
	-webkit-user-select: none;  /* Chrome all / Safari all */
	-moz-user-select: none;     /* Firefox all */
	-ms-user-select: none;      /* IE 10+ */
	user-select: none;          /* Likely future */
}


@mixin boderRadius($value)
{
	-webkit-border-radius: $value;
	-moz-border-radius: $value;
	border-radius: $value;
}

@mixin borderCorner ($value) {
	-webkit-border-radius: $value;
	-moz-border-radius: $value;
	border-radius: $value;
}

@mixin setOpacity ($value) {
	opacity: $value;
	filter: alpha(opacity=$value*100);
}

@mixin backgroundCover()
{
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
}

@mixin displayFlex($jc: center, $ai: center)
{
	display: -webkit-box;      /* OLD - iOS 6-, Safari 3.1-6 */
	display: -moz-box;         /* OLD - Firefox 19- (buggy but mostly works) */
	display: -ms-flexbox;      /* TWEENER - IE 10 */
	display: -webkit-flex;     /* NEW - Chrome */
	display: flex;             /* NEW, Spec - Opera 12.1, Firefox 20+ */

	-webkit-justify-content: $jc; /* Safari 6.1+ */
	justify-content: $jc;

	-webkit-align-items: $ai; /* Safari 7.0+ */
	align-items: $ai;
}

@mixin objAbsoluteMiddle() {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%); /* IE 9 */
	-webkit-transform: translate(-50%, -50%); /* Safari */
}

@mixin clearObjAbsoluteMiddle() {
	position: relative;
	top: auto;
	left: auto;
	transform: translate(0, 0);
	-ms-transform: translate(0, 0); /* IE 9 */
	-webkit-transform: translate(0, 0); /* Safari */
}

@mixin Rotate($value)
{
	-ms-transform: rotate($value+deg); /* IE 9 */
	-moz-transform: rotate($value+deg);
	-o-transform: rotate($value+deg);
	-webkit-transform: rotate($value+deg); /* Chrome, Safari, Opera */
	transform: rotate($value+deg);
}

@mixin clearfix {
	&:after {
		content: "";
		display: table;
		clear: both;
	}
}


@mixin noselect {
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none;   /* Chrome/Safari/Opera */
	-khtml-user-select: none;    /* Konqueror */
	-moz-user-select: none;      /* Firefox */
	-ms-user-select: none;       /* Internet Explorer/Edge */
	user-select: none;           /* Non-prefixed version, currently
                                  not supported by any browser */
}

@mixin boxShadow($value) {
	-webkit-box-shadow: $value;
	-moz-box-shadow: $value;
	box-shadow: $value;
}

@mixin customTransform($value)
{
	-ms-transform: $value; /* IE 9 */
	-moz-transform: $value;
	-o-transform: $value;
	-webkit-transform: $value; /* Chrome, Safari, Opera */
	transform: $value;
}

@mixin appearanceNone() {
	appearance: none;
	-moz-appearance: none;
	-webkit-appearance: none;
}


